

import axios from 'axios';
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name:"my",
data () {
return {
userInfo:localStorage.userInfo ? JSON.parse(localStorage.userInfo) : ""
}
},
components: {
Group,
Cell,
XButton
},
methods:{
logout(){
sessionStorage.userId = "";
this.$vux.confirm.show({
title:"提示",
content:"确定要退出系统?",
onCancel () {

},
onConfirm: _ => {
this.$router.push({path:"/"});
}
})

},
handleUpload(e){
var file = e.target.files[0];
if(!/image\/\w+/.test(file.type)){ //判断获取的是否为图片文件
this.$vux.toast.text("请确保文件为图像文件",'top');
return false;
}
if(file.size > 1048576){
this.$vux.toast.text("请上传小于1M以内的图片",'top');
return false;
}
var fd = new FormData()
fd.append('file', file)
this.$http.post({
url:"/uploadFile",
data:fd,
type:"formdata",
success: data => {
this.setUploadImg(data.filePath);
},
error: msg => {
this.$vux.toast.text(msg, 'top');
}
})
},
setUploadImg(imgPath){
this.$http.post({
url:"/my/upload",
type:"json",
data:{
id:this.userInfo.id,
pic:imgPath
},
success: data => {
if(process.env.NODE_ENV == "development"){
this.userInfo.pic = "http://localhost:8000" + imgPath;
}else{
this.userInfo.pic = imgPath;
};
localStorage.userInfo = JSON.stringify(this.userInfo);
},
error: msg => {
this.$vux.toast.text(msg, 'top');
}
})
}
}
}



